{title: '（1）您精力充沛吗？（指精神头足，乐于做事）',info: '很少（有一点/偶尔）',},
{title: '（2）您容易疲乏吗？（指体力如何，是否稍微活动一下或做一点家务劳动就感到累）',info: '很少（有一点/偶尔）',},
{title: '（3）您容易气短，呼吸短促，接不上气吗？',info: '很少（有一点/偶尔）',},
{title: '（4）您说话声音低弱无力吗？（指说话没有力气）',info: '很少（有一点/偶尔）',},
{title: '（5）您感到闷闷不乐、情绪低沉吗？（指心情不愉快、情绪低落）',info: '很少（有一点/偶尔）',},
{title: '（6）您容易精神紧张、焦虑不安吗？（指遇事是否心情紧张）',info: '很少（有一点/偶尔）',},
{title: '（7）您因为生活状态改变而感到孤独、失落吗？',info: '很少（有一点/偶尔）',},
{title: '（8）您容易感到害怕或受到惊吓吗？',info: '很少（有一点/偶尔）',},
{title: '（9）您感到身体超重不轻松吗？（感觉身体沉重）[BMI指数=体重(kg)/身高2(m)]',info: '很少（有一点/偶尔）',},
{title: '（10）您眼睛干涩吗？',info: '很少（有一点/偶尔）',},
{title: '（11）您手脚发凉吗？（不包含因周围温度低或穿的少导致的手脚发冷）',info: '很少（有一点/偶尔）',},
{title: '(12）您胃脘部、背部或腰膝部怕冷吗？（指上腹部、背部、腰部或膝关节等，有一处或多处怕冷）',info: '很少（有一点/偶尔）',},
{title: '（13）您比一般人耐受不了寒冷吗？（指比别人容易害怕冬天或是夏天的冷空调、电扇等）',info: '很少（有一点/偶尔）',},
{title: '（14）您容易患感冒吗？（指每年感冒的次数）',info: '很少（有一点/偶尔）',},
{title: '（15）您没有感冒时也会鼻塞、流鼻涕吗？',info: '很少（有一点/偶尔）',},
{title: '（16）您有口粘口腻，或睡眠打鼾吗？',info: '很少（有一点/偶尔）',},
{title: '（17）您容易过敏（对药物、食物、气味、花粉或在季节交替、气候变化时）吗？',info: '很少（有一点/偶尔）',},
{title: '（18）您的皮肤容易起荨麻疹吗？（包括风团、风疹块，风疙瘩）',info: '很少（有一点/偶尔）',},
{title: '（19）您的皮肤在不知不觉中会出现紫瘀斑、皮下出血吗？（指皮肤在没有外伤的情况下出现青一块紫一块的情况）',info: '很少（有一点/偶尔）',},
{title: '（20）您的皮肤一抓就红，并出现抓痕吗？（指被指甲或钝物划过后皮肤的反应）',info: '很少（有一点/偶尔）',},
{title: '（21）您皮肤或口唇干吗？',info: '很少（有一点/偶尔）',},
{title: '（22）您有肢体麻木或固定部位疼痛的感觉吗？',info: '很少（有一点/偶尔）',},
{title: '（23）您面部或鼻部有油腻或者油亮发光吗？（指脸上或鼻子）',info: '很少（有一点/偶尔）',},
{title: '（24）您面色或目眶晦暗，或出现褐色斑块/斑点吗？',info: '很少（有一点/偶尔）',},
