{title: '（28）您腹部肥大吗？（指腹部脂肪肥厚）/斑点吗？',info: '很少（有一点/偶尔）',},
{title: '（29）您吃（喝）凉的东西感到不舒服或者怕吃（喝）凉的东西吗？（指不喜欢吃凉的食物，或吃了凉的食物后会不舒服）/斑点吗？',info: '很少（有一点/偶尔）',},
{title: '（30）您有大便黏滞不爽。解不尽的感觉吗？（大便容易黏在马桶或便坑壁上）',info: '很少（有一点/偶尔）',},
{title: '（31）您容易大便干燥吗？',info: '很少（有一点/偶尔）',},
{title: '（32）您舌苔厚腻或有舌苔厚厚的感觉吗？（如果自我感觉不清楚可由调查员观察后填写）',info: '很少（有一点/偶尔）',},
{title: '（33）您舌下静脉瘀紫或者增粗吗？（可由调查员辅助观察后填写）',info: '很少（有一点/偶尔）',},
