},
mounted(){
var routerParams = this.$route.params;
this.RECORD_ID = routerParams.RECORD_ID;
this.$vux.loading.show()
this.initInfo();
},
beforeRouteLeave(to, from, next) { // 用于做缓存

if (to.name == 'PhysicalQualityReport') {

localStorage.setItem('RECORD_ID', this.$route.params.RECORD_ID || window.localStorage.RECORD_ID)
} else {
localStorage.removeItem('RECORD_ID');
}
next();
},
methods: {
getCPSJ(CPSJ){
var date = new Date(CPSJ);
var Y = date.getFullYear() + '年'
var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '月'
var D = date.getDate() + '日'

return Y+M+D;

},
starActivity(RECORD_ID){
this.$router.push({name: "PhysicalQualityReport", params: {RECORD_ID: RECORD_ID}});
},
initInfo(){

this.$ajax.post('',{
'MSH.1':'lnrzyjkgl',
'MSH.2':'xq',
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM':this.$root.loginInfo.CHZBM,
'RECORD_ID':this.$route.params.RECORD_ID || window.localStorage.RECORD_ID,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$vux.loading.hide()
if(typeof data !='undefined'){
this.objData = data;
this.quesstionList[0].info=this.row1[data.WTJLCP];
this.quesstionList[1].info=this.row2[data.WTRYPL];
this.quesstionList[2].info=this.row2[data.WTRYJD];
this.quesstionList[3].info=this.row1[data.WTSYWL];
this.quesstionList[4].info=this.row1[data.WTQXDC];
