this.quesstionList[6].info=this.row1[data.WTGDSL];
this.quesstionList[7].info=this.row1[data.WTHPSJ];
this.quesstionList[8].info=this.row3[data.WTBMI];
this.quesstionList[9].info=this.row2[data.WTYJGS];
this.quesstionList[10].info=this.row1[data.WTSJFL];
this.quesstionList[11].info=this.row2[data.WTSTPL];
this.quesstionList[12].info=this.row2[data.WTNSHL];
this.quesstionList[13].info=this.row2[data.WTRYGMA];
