this.quesstionList[15].info=this.row1[data.WTKNDH];
this.quesstionList[16].info=this.row2[data.WTRYGMI];
this.quesstionList[17].info=this.row2[data.WTRYXMZ];
this.quesstionList[18].info=this.row1[data.WTQZYB];
this.quesstionList[19].info=this.row1[data.WTRCZH];
this.quesstionList[20].info=this.row1[data.WTPFKCGZ];
this.quesstionList[21].info=this.row1[data.WTZTMM];
this.quesstionList[22].info=this.row1[data.WTMBYN];
this.quesstionList[23].info=this.row1[data.WTMKHA];
this.quesstionList[24].info=this.row1[data.WTPFSZ];
this.quesstionList[25].info=this.row1[data.WTKGYZ];
this.quesstionList[26].info=this.row1[data.WTKQYW];
this.quesstionList[27].info=this.row4[data.WTFBFD];
this.quesstionList[28].info=this.row1[data.WTCWYLSBS];
this.quesstionList[29].info=this.row1[data.WTDBNZ];
this.quesstionList[30].info=this.row1[data.WTDBGZ];
this.quesstionList[31].info=this.row1[data.WTSTHN];
this.quesstionList[32].info=this.row1[data.WTSXJMYZ];
}
}).catch(error => {
console.log(error);
});

}
}
}




